  methods: {
    getDataemit(param) {
      // alert("this url is : " + param.urlpost);
      this.$emit("yoy", {
        data: param.urlpost,
      });
    },
    getData: function() {
      this.$http
        .get("/posts/all/trend")
        .then((res) => {
          this.dataPost = res.data;
        })
        .catch((err) => {
          alert(err.message);
        });
    },
  },
  mounted() {
    this.getData();
  },
